<style scoped lang="less">
  @import "./tag.less";
</style>

<template>
  <div class="tag">
  	<Row :gutter="20">
     	<Col :sm="8" :md="6" :lg="4"><Input v-model="queryText" placeholder="请输入标签名称"></Input></Col>
     	<Col :sm="8" :md="8" :lg="5">
     		<Button type="primary" icon="search" shape="circle" style="margin-right:10px;">搜索</Button>
     		<Button type="primary" icon="plus" shape="circle" @click="add">新增</Button>
     		<Modal
	        v-model="tagModal"
	        title="新增标签"
	        @on-ok="addOrEdit"
	        @on-cancel="tagModal=false">
	        <p>Content of dialog</p>
 		    </Modal>
     	</Col>
    </Row>
    <baseTable
			@on-edit="edit" 
			@on-delete="remove" 
			@on-page-change="onCurrentPageChange"
			@on-size-change="onPageSizeChange"
			:columnList="tableColumn" 
			:total="total"
			:currentPage="currentPage"
			:pageSize="pageSize"
			:loading="tableLoading"
			v-model="tableData"></baseTable>
  </div>
</template>

<script>
import allTableColumn from '@/common/js/table-column'
import baseTable from '@/components/table/base-table'
export default {
  name: 'tag',
  data(){
  	return {
    	queryText: '',
			tagModal: false,
			tableLoading: false,
			currentPage: 1,
			pageSize: 10,
			total: 0,
		  tableColumn: [],
		  tableData: [
		    {
		      index: '1',
		      name: 'vue',
		      desc: '尤小右',
		      count: 20,
		      createTime: '2018-02-08 16:17'
		    }
		  ]
	  }
  },
  created(){
		this.tableColumn = allTableColumn.tagTableColumn
	},
	mounted(){},
	methods:{
    onCurrentPageChange(page){},
		onPageSizeChange(size){},
		// 新增
    add(){
    	this.linkModal = true
		},
		edit(row, index){
      this.linkModal = true
    },
    addOrEdit(){
      this.$Message.info('Clicked ok');
		},
		// info(row){
		// 	console.log(row)
		// },
		remove(row, index){
      this.tableData.splice(index, 1);
    },
  },
  components: {
    baseTable
  }
}
</script>



